<template>
  <div class="bear-body">
    <div class="bear-head" :style="{'background-image': 'url('+bgurl+')'}">
      <div class="bear-tips" v-if="(tipArray[Math.floor(Math.random()*9+1)])"></div>
      <audio id="bear-audio" :src="music"></audio>
      <div class="bear-information">
        <!-- 头像 -->
        <div class="bear-information__portrait">
          <div class="bear-information__play"  @click="playMusic">
            <i class="iconfont" :class="{'icon-iconfront-':playIn,'icon-bofang':!playIn}"></i>
          </div>
          <img src="../../../static/image/bear.png" :class="{'bear-rotate': playIn }" alt="大熊Bear" />
          <div class="bear-audio__time" v-if="playIn">{{currentTime}}</div>
        </div>
        <div class="bear-information__tips">敌进我退，敌驻我扰，敌疲我打，敌退我追 </div>
        <!-- 个人资料下面图标按钮 -->
        <div class="bear-information__bottom">
          <div>
            <i class="iconfont icon-github" @click="linkRelevant('github')"></i>
          </div>
          <div>
            <i class="iconfont icon-zhihu" @click="linkRelevant('zhihu')"></i>
          </div>
          <div>
            <i class="iconfont icon-weibo" @click="linkRelevant('weibo')"></i>
          </div>
        </div>
      </div>
    </div>
    <div id="bear-box" class="autoHeight">
      <div class="bear-index" v-for="(item, index) in imgArray">
        <!-- <img :src="item" alt /> -->
        <div class="bear-box__img" :data-src="item" :style="{'background-image':'url('+item+')'}"></div>
        <img :src="item" :data-src="item" alt />
      </div>
    </div>
    <div class="bear-footer">
      <a href="javascript:;">@大熊Bear</a> &nbsp;&nbsp;&nbsp;蜀ICP备17041173号-1
    </div>
  </div>
</template>

<script>
import bg0 from "@/assets/image/bg.jpg";
import bg1 from "@/assets/image/bg1.jpg";
import bg2 from "@/assets/image/bg2.jpg";
import bg3 from "@/assets/image/bg3.jpg";
import bg4 from "@/assets/image/bg4.jpg";
import bg5 from "@/assets/image/bg5.jpg";
import bg6 from "@/assets/image/bg6.jpg";
import bg7 from "@/assets/image/bg7.jpg";
import bg8 from "@/assets/image/bg8.jpg";
import bg9 from "@/assets/image/bg9.jpg";
import Viewer from "viewerjs";

import "viewerjs/dist/viewer.css";
export default {
  name: "bear-head",
  data() {
    return {
      imgList: [bg0, bg1, bg2, bg3, bg4, bg5, bg6, bg7, bg8, bg9],
      imgArray: [],
      msg: "",
      bgurl: "",
      music: "../../../static/audio/TheStrumbellas__Spirits.mp3",
      playIn: false,
      tipArray: ["敌进我退，敌驻我扰，敌疲我打，敌退我追"],
      currentTime: '加载中……',
      duration: 0,
    };
  },
  created() {
    // 随机 0-9 的图片作为 背景图 需要require才行
    let bg = Math.floor(Math.random() * 9 + 1);
    this.bgurl = require("../../assets/image/bg" + bg + ".jpg");
    //  默认三个随机数
    let MathArray = [];
    for (var i = 0; i <= 2; i++) {
      this.imgArray.push(this.imgList[Math.floor(Math.random() * 9 + 1)]);
    }
    console.log(this.imgArray);
  },
  watch: {
     
  },
  mounted() {
    const ViewerDom = document.getElementById("bear-box");

    const viewer = new Viewer(ViewerDom, {
      url: "data-src"
    });
  },
  methods: {
    linkRelevant(icon) {
      let link = "";
      switch (icon) {
        case "github":
          link = "";
          break;
        case "zhihu":
          link = "";
          break;
        case "weibo":
          link = "https://weibo.com/u/3952262561";
          break;
      }
      window.location.href = link
    },
    playMusic () {
      this.playIn = !this.playIn
      let bearMusic = document.getElementById('bear-audio')
      if (this.playIn) {
        bearMusic.play()
      } else {
        bearMusic.pause()
      }
      
      //  播放总时长
      this.duration = parseInt(bearMusic.duration)
      let that = this;
      let interval = setInterval( function(){
        //  播放进度赋值
        if(that.transTime(bearMusic.currentTime) !== '00:00' && parseInt(bearMusic.currentTime) < that.duration){
          that.currentTime = '播放时长:' + that.transTime(bearMusic.currentTime)
        }else if(parseInt(bearMusic.currentTime) == that.duration){
          that.playIn = false
          window.clearInterval(interval)
        }
      },1000)
    },
    transTime(time) {
        var duration = parseInt(time);
        var minute = parseInt(duration/60);
        var sec = duration%60+'';
        var isM0 = ':';
        if(minute == 0){
            minute = '00';
        }else if(minute < 10 ){
            minute = '0'+minute;
        }
        if(sec.length == 1){
            sec = '0'+sec;
        }
        return minute+isM0+sec
    }
  }
};
</script>

<style lang="scss" scoped>
.bear-body {
  padding-bottom: 35px;
  .bear-head {
    position: relative;
    top: 0;
    .bear-tips {
      font-family: "Helvetica Neue", Helvetica, "PingFang SC",
        "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
      width: 50%;
      height: 60px;
      line-height: 60px;
      font-size: 45px;
      color: #fff;
      opacity: 0.9;
      position: absolute;
      left: 25%;
      top: calc(280px - 30px);
      background: rgba(255, 255, 255, 0.2);
      text-align: center;
      border: 1px solid #fff;
      border-radius: 5px;
    }
    // 个人资料
    .bear-information {
      width: 50%;
      height: 50%;
      background: #18c6e4;
      border-radius: 20px;
      position: absolute;
      top: 50%;
      transform: translateY(-10%);
      left: 25%;
      opacity: 0.7;
      @media screen and (max-width: 1000px) {
        height: 30%;
        width: 80%;
        left: 10%;
      }
      // 头像
      .bear-information__portrait {
        height: 80px;
        width: 80px;
        position: absolute;
        top: -40px;
        left: 50%;
        transform: translateX(-50%); // 自身像素的 负 百分之50;
        border-radius: 50%;
        &:hover{
          .bear-information__play{
            display: block;
          }
        }
        .bear-information__play{
          display: none;
          width: 100%;
          height: 100%;
          border-radius: 50%;
          background: rgba(230,193,184,0.6);
          position: absolute;
          top:0;
          left: 0;
          text-align: center;
          line-height: 80px;
          z-index: 99;
          i.iconfont{
            font-size: 25px;
            opacity: 1;
          }
        }
        img {
          width: 100%;
          height: 100%;
          border-radius: 50%;
        }
        img.bear-rotate{
          transition: 0.5s;
          animation: bearRotate 10s linear infinite;
        }
        .bear-audio__time{
          height: 20px;
          line-height: 20px;
          text-align: center;
          width: 120px;
          position: absolute;
          bottom: -20px;
          left: -20px;
          color: #fff;
        }
      }
      .bear-information__tips {
        width: 100%;
        line-height: 25px;
        text-align: center;
        font-size: 15px;
        color: #fff;
        opacity: 1;
        position: absolute;
        top: 50%;
      }
      .bear-information__bottom {
        display: flex;
        width: 50%;
        height: 60px;
        position: absolute;
        bottom: 0;
        left: 25%;
        div {
          flex: 1;
          line-height: 60px;
          text-align: center;
          i.iconfont {
            font-size: 20px;
            &:hover {
              color: #d61ea6;
            }
          }
        }
      }
    }
  }
  #bear-box {
    width: 100%;
    .bear-index {
      width: 33.333%;
      float: left;
      height: 170px;
      position: relative;
      .bear-box__img{
        display: none;
        width: 100%;
        height: 100%;
        position: absolute;
        top:0;left:0;
      }
      img {
        width: 100%;
        height: 100%;
        position: absolute;
        top:0;left:0;
      }
    }
  }
  .bear-footer {
    width: 100%;
    height: 40px;
    line-height: 40px;
    background-image: linear-gradient(90deg, #1dd8f8 0%, #05a1f3 100%);
    position: fixed;
    bottom: 0;
    left: 0;
    color: #fff;
    text-align: center;
    font-size: 14px;
    a {
      text-decoration: none;
      color: #fff;
      &:hover {
        color: #d61ea6;
      }
    }
  }
}
@keyframes bearRotate{
  from{
    transform:rotate(0)
  }
  to{
    transform:rotate(360deg)
  }
}

</style>



